import React, {useEffect, useState} from 'react';

export const WaitingRoomAvatar = (props) => {
    const [diameter, setDiameter] = useState(89);
    useEffect(() => {
    
        window.addEventListener('load', () => {
            if(window.innerWidth >= 1024){
           
                let width = window.innerWidth * (6.2/100);
        
              
                setDiameter(width);
            }else if(window.innerWidth < 768){
                setDiameter(60);
            }
        });
 
        return () => window.removeEventListener('load', () => {
            if(window.innerWidth >= 1024){

                let width = window.innerWidth * (6.2/100);
        

                setDiameter(width);
            }else if(window.innerWidth < 768){
                setDiameter(60);
            }
        });
    }, []) 
    return (
        
            <svg width={`${diameter}`} height= {`${diameter}`}/* "89" */ viewBox="0 0 89 89" fill="none" xmlns="http://www.w3.org/2000/svg" xlinkHref="http://www.w3.org/1999/xlink">
            <circle cx="44.5" cy="44.5" r="44.5" fill="url(#pattern0)"/>
            <defs>
            <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
            <use xlinkHref="#image0" transform="scale(0.0031348)"/> 
            </pattern>
            <image id="image0" width="319" height="319" xlinkHref="data:image/png;base64,"/>
            </defs>
            </svg>

       
    );
};